
html, body {
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
header {
  overflow: hidden;
  height: 70px;
  background: #eee;
}
.logo {
  float: left;
  margin: 10px;
  width: 50px;
}
button {
  width: 240px;
  height: 50px;
  line-height: 50px;
  font-size: 32px;
  margin: 10px;
  color: white;
  background: #ff6600;
  border: none;
}
button:hover {
  background: #ff7700;
}
button:active {
  background: #ee5500;
}
header button {
  float: left;
  margin-top: 30px;
  width: auto;
  height: 24px;
  line-height: 20px;
  font-size: 16px;
  background: #41b883;
}
header button:hover {
  background: #51c893;
}
header button:active {
  background: #31a873;
}
textarea {
  width: 800px;
  height: 320px;
}



